#homepage{
    background-color:  green;
   
    padding-bottom: 13%;
}

.Services {
    padding: 60px 60px;

    background: green;
    font-family: Poppins;
}

.Services .titlepage {
    text-align: center;
}

.Services .titlepage h2 {
    color: #fff;
    padding-bottom: 20px;
}

.Services .titlepage p {
    color: #fff;
}

.Services .Services-box i {
    background: #fff;
    width: 100px;
    height: 100px;
    border-radius: 50px;
    display: inline-block;
    padding-top: 19px;
    overflow: hidden;
}

.Services .Services-box i img{
    height: 80%;
}

.Services .Services-box i:hover {
    background: #ddd;
}

.Services .Services-box h3 {
    padding: 20px 0px 20px 0px;
    font-size: 20px;
    line-height: 25px;
    color: #fff;
    display: block;
    font-weight: 500;
}

.Services .Services-box {
    text-align: center;
    background-color: transparent;
    border-radius: 20px;
    border: #cecfd7 solid 2px;
    padding: 40px;
    transition: ease-in all 0.5s;
    margin-bottom: 30px;
}

.Services .Services-box:hover {
    background: lightgreen;
    transition: ease-in all 0.5s;
    cursor: pointer;
}

.Services .Services-box p {
    font-size: 16px;
    line-height: 24px;
    color: #fff;
}

